import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";
import { candidateWorkExperienceToDisplay } from "app/recruitment/mapper/candidate/candidate-work-experience";
import {
  ICandidateWorkExperienceDisplayModel,
  ICandidateWorkExperienceModel,
  ICandidateWorkExperienceRequest,
} from "app/recruitment/models/candidate/work-experience/work-experience";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "candidate-work-experience-form",
  templateUrl: "./candidate-work-experience-form.component.html",
  styleUrls: ["./candidate-work-experience-form.component.scss"],
})
export class CandidateWorkExperienceFormComponent
  extends AppHelpers
  implements OnInit
{
  @Input() candidateId: number = 0;
  @Output() changeDescription = new EventEmitter<ITabDescriptionInfo>();
  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    // "Ser",
    "company",
    "department",
    "job",
    "startDate",
    "endDate",
    "companySalary",
    "resignationReason",
    "notes",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<ICandidateWorkExperienceDisplayModel>;

  resignationReasonOtherId: number = 6;
  selectedResignationReason: Idropdown;

  constructor(private candidateService: CandidateService) {
    super();

    this.dataSource = new MatTableDataSource([]);
    this.candidateService.submitted$.subscribe((submitted) => {
      if (this.dataSource.data.length == 0) this.submitted = submitted;
    });
  }

  // override onChangeBusinessAndCompanyId() {
  //   if (this.dataSource?.data?.length > 0)
  //     this.dataSource = new MatTableDataSource(null);

  //   if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
  //     this.getAll();
  //   } else this.isLoaded = true;
  // }

  ngOnInit() {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        companyName: [null /* , [Validators.required] */],
        org_Name: [null /* , [Validators.required] */],
        job_Title_Name: [null /* , [Validators.required] */],
        from_Date: [null, [Validators.required]],
        to_Date: [null, [Validators.required]],
        companySalary: [null, [CustomValidators.gt(0)]],
        resignationReasonId: [null /* , [Validators.required] */],
        resignation_Reason: [null],
        note: [null],
      },
      {
        validator: CustomValidator.compareDates("from_Date", "to_Date"),
      }
    );

    this.dataSource = new MatTableDataSource([]);
    this.invalidRequiredCount();
    this.isLoaded = true;
  }

  get formCtrls() {
    return this.form.controls;
  }

  // getAll() {
  //   this.dataSource = new MatTableDataSource(null);
  //   this.isLoaded = false;
  //   this.candidateWorkExperienceService
  //     .getAll(this.candidateId)
  //     .subscribe((res: IResponse<ICandidateWorkExperienceModel[]>) => {
  //       if ((res.message.messageType = MessageTypes.Success)) {
  //         this.dataSource = new MatTableDataSource(
  //           candidateWorkExperienceToDisplay(
  //             res.data,
  //             this.resignationReasonOtherId
  //           )
  //         );
  //         if (res.data.length > 0) this.changeDescriptionInfo(true, 0);
  //         else this.invalidRequiredCount();
  //         this.isLoaded = true;
  //       } else this.errorList(res.message.messages);
  //     });
  // }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ICandidateWorkExperienceRequest = {
        // person_Instance_ID: this.candidateId,
        id: this.form.value.id == 0 ? this.getNewId() : this.form.value.id,
        companyName: this.form.value.companyName,
        org_Name: this.form.value.org_Name,
        job_Title_Name: this.form.value.job_Title_Name,
        from_Date: HelpersFunctions.mapDateReverse(this.form.value.from_Date),
        to_Date: HelpersFunctions.mapDateReverse(this.form.value.to_Date),
        companySalary: this.form.value.companySalary,
        resignationReasonId: this.form.value.resignationReasonId,
        resignation_Reason: null,
        note: this.form.value.note,
      };
      if (model.resignationReasonId == this.resignationReasonOtherId)
        model.resignation_Reason = this.form.value.resignation_Reason;

      const indexModel: ICandidateWorkExperienceModel = {
        ...model,
        core_Resignation_Reason: this.selectedResignationReason,
      };

      this.form.value.id == 0
        ? this.create(indexModel)
        : this.update(indexModel);
    }
    // else this.toastr.error("General.formNotValid");
  }

  create(model: ICandidateWorkExperienceModel) {
    // this.showLoading();
    // this.candidateWorkExperienceService
    //   .create(model)
    //   .subscribe((res: IResponse<ICandidateWorkExperienceModel>) => {
    //     if (res?.message?.messageType == MessageTypes.Success) {
    //       this.addSuccessfullyMsg(res?.message?.messageTypeName);
    //       this.clearForm();
    //       this.getAll();
    //     } else this.errorList(res.message.messages);
    //     this.hideLoading();
    //   });

    this.dataSource.data.push(
      candidateWorkExperienceToDisplay(
        [model],
        this.resignationReasonOtherId
      )[0]
    );
    this.clearForm();
    this.invalidRequiredCount();
    this.updateGeneralData();
  }

  update(model: ICandidateWorkExperienceModel) {
    // this.showLoading();
    // this.candidateWorkExperienceService
    //   .update(model)
    //   .subscribe((res: IResponse<ICandidateWorkExperienceModel>) => {
    //     if (res?.message?.messageType == MessageTypes.Success) {
    //       this.updateSuccessfullyMsg(res?.message?.messageTypeName);
    //       this.clearForm();
    //       this.getAll();
    //     } else this.errorList(res.message.messages);
    //     this.hideLoading();
    //   });

    let mapedModel: ICandidateWorkExperienceDisplayModel =
      candidateWorkExperienceToDisplay(
        [model],
        this.resignationReasonOtherId
      )[0];
    this.dataSource.data[this.getIndex(mapedModel)] = mapedModel;
    this.clearForm();
    this.updateGeneralData();
  }

  candidateeWorkExperienceToEdit(
    workExperience: ICandidateWorkExperienceDisplayModel
  ) {
    this.form.patchValue(workExperience);
  }

  onDelete(workExperience: ICandidateWorkExperienceDisplayModel): void {
    let msg = `${workExperience.job} (${workExperience.startDate} - ${workExperience.endDate})`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(workExperience);
    });
  }

  delete(workExperience: ICandidateWorkExperienceDisplayModel) {
    // this.showLoading();
    // this.candidateWorkExperienceService
    //   .Delete(workExperience.id)
    //   .subscribe((res: IResponse) => {
    //     if (res.message.messageType == MessageTypes.Success) {
    //       this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
    //       this.getAll();
    //       if (workExperience.id == this.formCtrls.id.value) this.clearForm();
    //     } else this.errorList(res?.message?.messages);
    //     this.hideLoading();
    //   });

    this.dataSource.data.splice(this.getIndex(workExperience), 1);
    this.invalidRequiredCount();
    if (this.formCtrls.id.value == workExperience.id) this.clearForm();
    this.updateGeneralData();
  }

  updateGeneralData() {
    let generalModel: ICandidateWorkExperienceRequest[] = [];
    this.dataSource.data.forEach((ele) => {
      let workExperience = { ...ele };
      delete workExperience.id;
      delete workExperience.company;
      delete workExperience.department;
      delete workExperience.job;
      delete workExperience.startDate;
      delete workExperience.endDate;
      delete workExperience.resignationReason;
      delete workExperience.notes;
      delete workExperience.core_Resignation_Reason;
      generalModel.push(workExperience);
    });
    this.candidateService.candidateData$.next({
      ...this.candidateService.candidateData$.getValue(),
      WorkExperincesData: generalModel,
    });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getIndex(certification: ICandidateWorkExperienceDisplayModel): number {
    return this.dataSource.data.findIndex((ele) => ele.id == certification.id);
  }

  getNewId(): number {
    return (
      this.dataSource.data.reduce(
        (acc, ele) => (acc = acc > ele.id ? acc : ele.id),
        0
      ) + 1
    );
  }

  onChangeResignationReason(values: Idropdown[]) {
    if (values.length > 0 && values[0].id == this.resignationReasonOtherId)
      this.formCtrls.resignation_Reason.setValidators([Validators.required]);
    else this.formCtrls.resignation_Reason.clearValidators();
    this.formCtrls.resignation_Reason.updateValueAndValidity();

    this.selectedResignationReason = values.length > 0 ? values[0] : null;
    this.invalidRequiredCount();
  }

  descriptionInfo: ITabDescriptionInfo;
  changeDescriptionInfo(isDone: boolean, noOfRequiredInputs: number) {
    this.changeDescription.emit({
      isDone: isDone,
      noOfRequiredInputs: noOfRequiredInputs,
    });

    this.candidateService.isDone$.next({
      ...this.candidateService.isDone$.getValue(),
      // WorkExperincesData: this.descriptionInfo.isDone,
      WorkExperincesData: isDone,
    });
  }

  invalidRequiredCount() {
    if (!(this.dataSource?.data?.length > 0)) {
      let count = HelpersFunctions.noOfInvalidAndRequiredInputs(this.form);
      this.changeDescriptionInfo(count == 0 ? true : false, count);
    } else this.changeDescriptionInfo(true, 0);
  }
}
