import {
  ICandidateListDisplayModel,
  ICandidateListModel,
} from "app/recruitment/models/candidate/candidate";
import { msgTranslate } from "app/shared/resources/helpers-functions";

export function candidateToDisplay(
  data: ICandidateListModel[]
): ICandidateListDisplayModel[] {
  return data.map((candidate) => {
    return <ICandidateListDisplayModel>{
      ...candidate,
      fullName: msgTranslate(candidate.ar_Name, candidate.en_Name),
      jobTitleName: msgTranslate(
        candidate.jobTitleNameAr,
        candidate.jobTitleName
      ),
      organization: msgTranslate(
        candidate.organizationAr,
        candidate.organization
      ),
      statusProperty: msgTranslate(
        candidate.statusPropertyAr,
        candidate.statusProperty
      ),

      phone: candidate.contacts?.map((contact) => contact.value).join(", "),
      photo: candidate.img ? "/" + candidate.img : "",
    };
  });
}
