import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-candidate-document-dialog',
  templateUrl: './candidate-document-dialog.component.html',
  styleUrls: ['./candidate-document-dialog.component.scss']
})
export class CandidateDocumentDialogComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<CandidateDocumentDialogComponent>
  ) {}

  ngOnInit(): void {}

  onCloseDialog() {
    this.dialogRef.close();
  }


}
