import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { AppHelpers } from 'app/shared/resources/app-helpers';
import { ViewsManager } from 'app/shared/resources/views-manager';

@Component({
  selector: 'candidate-contacts-table',
  templateUrl: './candidate-contacts-table.component.html',
  styleUrls: ['./candidate-contacts-table.component.scss']
})
export class CandidateContactsTableComponent  extends AppHelpers  implements OnInit {
  @Input() isMatPaginator: boolean = false;
  @Output() afterDelete = new EventEmitter<any>;
  dataSource: MatTableDataSource<any>;

  mandatoryColomns: string[] = [
    "contactType",
    "value",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean;

  constructor() {
    super();

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.candidate_contacts_sec
    );
  }

  ngOnInit(): void {
  }
  getList(page: number, pageSize?: number): void {
  }
  onDelete(contactInfo): void {
    let msg = this.msgTranslate(contactInfo.ar_Name,contactInfo.en_Name )
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(contactInfo);
    });
  }

  delete(contact) {

    this.showLoading('table');

  }
  toEdit(contact ) {
  }


}
