import { Component, OnInit } from "@angular/core";
import { MatDialogRef } from "@angular/material/dialog";

@Component({
  selector: "candidate-basic-info-dialog",
  templateUrl: "./candidate-basic-info-dialog.component.html",
  styleUrls: ["./candidate-basic-info-dialog.component.scss"],
})
export class CandidateBasicInfoDialogComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<CandidateBasicInfoDialogComponent>
  ) {}

  ngOnInit(): void {}

  onCloseDialog() {
    this.dialogRef.close();
  }
}
