import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-candidate-work-experience-dialog',
  templateUrl: './candidate-work-experience-dialog.component.html',
  styleUrls: ['./candidate-work-experience-dialog.component.scss']
})
export class CandidateWorkExperienceDialogComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<CandidateWorkExperienceDialogComponent>
  ) {}

  ngOnInit(): void {}

  onCloseDialog() {
    this.dialogRef.close();
  }


}
