import { Component, OnInit } from '@angular/core';
import { IGUIModel } from 'app/shared/interfaces/user-views';
import { AppHelpers } from 'app/shared/resources/app-helpers';
import { ViewsManager } from 'app/shared/resources/views-manager';
@Component({
  selector: 'candidate-related-requests-core',
  templateUrl: './candidate-related-requests-core.component.html',
  styleUrls: ['./candidate-related-requests-core.component.scss']
})
export class CandidateRelatedRequestsCoreComponent extends AppHelpers implements OnInit {
	allowedSections: IGUIModel[];
	viewsManager = ViewsManager;
	constructor() {
		super();
	}

  ngOnInit(): void {
  }
  override onModulesChanged() {
		this.allowedSections = this.getCustomViewChildren(
			this.getParentViewName,
			ViewsManager.candidate_Related_Requests_tab
		);
	}

}
