import {
  ICandidateCertificationDisplayModel,
  ICandidateCertificationModel,
} from "app/recruitment/models/candidate/education/Certifications";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";

export function candidateCertificationToDisplay(
  data: ICandidateCertificationModel[]
): ICandidateCertificationDisplayModel[] {
  return data.map((certification) => {
    return <ICandidateCertificationDisplayModel>{
      ...certification,
      certification: certification.certification_Name,
      location: certification.location_Name,
      startDate: HelpersFunctions.mapDate(certification.from_Date),
      endDate: HelpersFunctions.mapDate(certification.to_Date),
      notes: certification.note,
    };
  });
}
