import { Component, Input, OnInit } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";

@Component({
  selector: "candidate-general-info",
  templateUrl: "./candidate-general-info.component.html",
  styleUrls: ["./candidate-general-info.component.scss"],
})
export class CandidateGeneralInfoComponent
  extends AppHelpers
  implements OnInit
{
  @Input() isView: boolean = false;

  constructor() {
    super();
  }

  ngOnInit(): void {}

  descriptionInfo: ITabDescriptionInfo;

  changeDescriptionInfo(isDone: boolean, noOfRequiredInputs: number) {
    this.descriptionInfo = {
      isDone: isDone,
      noOfRequiredInputs: noOfRequiredInputs,
    };

  }
}
