import {
  ICandidateRelatedRequestsDisplayModel,
  ICandidateRelatedRequestsModel,
} from "app/recruitment/models/candidate/work-experience/related-requests";
import { msgTranslate } from "app/shared/resources/helpers-functions";

export function candidateRelatedRequestsToDisplay(
  data: ICandidateRelatedRequestsModel[]
): ICandidateRelatedRequestsDisplayModel[] {
  return data.map((jobInfo) => {
    return <ICandidateRelatedRequestsDisplayModel>{
      ...jobInfo,
      plan: msgTranslate(
        jobInfo?.rec_Plan?.ar_Name,
        jobInfo?.rec_Plan?.en_Name
      ),
      request: msgTranslate(
        jobInfo?.rec_Request?.job_Title?.ar_Name,
        jobInfo?.rec_Request?.job_Title?.en_Name
      ),
    };
  });
}
