import { msgTranslate } from "app/shared/resources/helpers-functions";
import {
  ICandidateContactDisplayModel,
  ICandidateContactModel,
} from "../../models/candidate/general-info/contacts";

export function candidateContactToDisplay(
  data: ICandidateContactModel[]
): ICandidateContactDisplayModel[] {
  return data.map((contact) => {
    return <ICandidateContactDisplayModel>{
      ...contact,
      contactType: msgTranslate(
        contact.comm_Contact?.comm_Contact_Type?.ar_Name,
        contact.comm_Contact?.comm_Contact_Type?.en_Name
      ),
      value: contact.comm_Contact?.value,
    };
  });
}
