import { Component, OnInit } from "@angular/core";
import { MatTableDataSource } from "@angular/material/table";
import { candidateToDisplay } from "app/recruitment/mapper/candidate/candidate";
import {
  ICandidateCardListDisplayModel,
  ICandidateCardListModel,
  ICandidateListDisplayModel,
  ICandidateListModel,
} from "app/recruitment/models/candidate/candidate";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { MatDialog } from "@angular/material/dialog";
import { DeleteCandidateDialogComponent } from "../delete-candidate-dialog/delete-candidate-dialog.component";

@Component({
  selector: "app-candidate-index",
  templateUrl: "./candidate-index.component.html",
  styleUrls: ["./candidate-index.component.scss"],
})
export class CandidateIndexComponent extends AppHelpers implements OnInit {
  isLoaded: boolean = false;
  isCardLoaded: boolean = false;

  isTable: boolean = true;
  dataSource: MatTableDataSource<ICandidateListDisplayModel>;
  mandatoryColomns: string[] = [
    "Ser",
    "id",
    "code",
    "photo",
    "fullName",
    "organization",
    "jobTitleName",
    "phone",
    "email",
    "actions",
  ];
  optionalColomns: string[] = [];
  candidateList: ICandidateCardListDisplayModel[] = [];

  constructor(public candidateService: CandidateService, private dialog: MatDialog
  ) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
      this.getListCard();
    }
  }

  ngOnInit(): void {}

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    this.removePageParams();
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.candidateService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<ICandidateListModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          if (res.data.length == 0 && page > 1) {
            this.getList(this.prevPage);
            return;
          }
          this.dataSource = new MatTableDataSource(
            candidateToDisplay(res.data)
          );
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        } else this.errorList(res?.message?.messages);
      });
  }
  getListCard() {
    this.isCardLoaded = false;

    this.candidateService
      .getListCard()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<ICandidateCardListModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {

          res.data.forEach((element) => {
            this.candidateList.push({
              requestID: element.requestID,
              jobTitleName: element.jobTitleName,
              jobTitleNameAr: element.jobTitleNameAr,
              candidateList: candidateToDisplay(element.candidateList),
              numOfCandidates: element.numOfCandidates,
              pendingNum: element.pendingNum,
              rejectedNum: element.rejectedNum,
              acceptededNum: element.acceptededNum,
              openVacances: element.openVacances,
            });
            this.isCardLoaded = true;
          });
        } else this.errorList(res?.message?.messages);
      });
  }
  onDelete(candidate: ICandidateListDisplayModel) {
    let msg = this.msgTranslate(candidate.ar_Name, candidate.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(candidate);
    });
  }
  openDeleteCardDialog(candidate: ICandidateListDisplayModel) {
    return this.dialog
      .open(DeleteCandidateDialogComponent, {
        autoFocus: false,
        disableClose: true,
        minWidth:600,
        data:{
          name: this.msgTranslate(candidate.ar_Name, candidate.en_Name)
        }
      })
      .afterClosed().subscribe((confirm)=>{
        if(confirm) this.delete(candidate);
      });
  }
  delete(candidate: ICandidateListDisplayModel) {
    this.showLoading();
    this.candidateService.Delete(candidate.id).subscribe((res: IResponse) => {
      if ((res.message.messageType = MessageTypes.Success)) {
        if(this.isTable){
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            this.getList(this.prevPage, this.page.pageSize);
          } else {
            this.getList(this.currentPage, this.page.pageSize);
          }
        }else{
          this.candidateList=[];
          this.getListCard();
        }

        this.deleteSuccessfullyMsg(res.message.messageTypeName);
      } else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }
  switchTable(switchTable: boolean) {
    this.isTable = switchTable;
    if (this.candidateList.length == 0) {
      this.getListCard();
    }
  }
}
