import { Component, OnInit } from '@angular/core';
import { IGUIModel } from 'app/shared/interfaces/user-views';
import { AppHelpers } from 'app/shared/resources/app-helpers';
import { ViewsManager } from 'app/shared/resources/views-manager';

@Component({
  selector: 'candidate-suitable-requisitions-core',
  templateUrl: './candidate-suitable-requisitions-core.component.html',
  styleUrls: ['./candidate-suitable-requisitions-core.component.scss']
})
export class CandidateSuitableRequisitionsCoreComponent  extends AppHelpers implements OnInit {
	allowedSections: IGUIModel[];
	viewsManager = ViewsManager;
	constructor() {
		super();
	}

  ngOnInit(): void {
  }
  override onModulesChanged() {
		this.allowedSections = this.getCustomViewChildren(
			this.getParentViewName,
			ViewsManager. candidate_Suitable_Requisitions_info_tab
		);
	}
}
