import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { CandidateGeneralInfoDialogComponent } from '../candidate-general-info-dialog/candidate-general-info-dialog.component';
import { AppHelpers } from 'app/shared/resources/app-helpers';
import { IGUIModel } from 'app/shared/interfaces/user-views';
import { ViewsManager } from 'app/shared/resources/views-manager';

@Component({
  selector: 'candidate-general-info-card',
  templateUrl: './candidate-general-info-card.component.html',
  styleUrls: ['./candidate-general-info-card.component.scss']
})
export class CandidateGeneralInfoCardComponent extends AppHelpers  implements OnInit {
  isLoaded: boolean = true;
  candidate:any;
  allowedSections: IGUIModel[];
  @Output() changeCandidateId = new EventEmitter<number>();

  @Output() onClickIcon? = new EventEmitter<any>();

  constructor() {
    super();
  }

  ngOnInit(): void {
  }

  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.candidate_general_info_tab
    );
  }

  openEditDialog(){

    let dialogRef = this.dialogsService.addEditDialog(
      CandidateGeneralInfoDialogComponent,
      this.candidate
    );
  }
  click($event) {
    this.onClickIcon.emit($event);
  }
}
