import { Component, OnInit, ViewChild } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { MatStepper } from "@angular/material/stepper";
import { ICandidateGenaralRequest } from "app/recruitment/models/candidate/candidate";
import { IRecruitmentRequestModel } from "app/recruitment/models/recruitment-request";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { RecruitmentRequestService } from "app/recruitment/services/recruitment-request.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { AddCandidateDoneDialogComponent } from "../add-candidate-done-dialog/add-candidate-done-dialog.component";

@Component({
  selector: "app-add-candidate-core",
  templateUrl: "./add-candidate-core.component.html",
  styleUrls: ["./add-candidate-core.component.scss"],
})
export class AddCandidateCoreComponent extends AppHelpers implements OnInit {
  // candidateId: number = 0;
  @ViewChild("stepper") stepper: MatStepper;

  requestId: number;
  isInternal: boolean = true;

  isGeneralCompleted: boolean = false;
  isEducationCompleted: boolean = false;
  isWorkExperienceCompleted: boolean = false;

  // in external candidate
  jobTitle: string;
  requestEndData: string;
  isLoaded: boolean = false;

  constructor(
    private candidateService: CandidateService,
    private recruitmentRequestService: RecruitmentRequestService,
    private dialog: MatDialog
  ) {
    super();

    this.candidateService.isDone$.subscribe((isDone) => {
      this.isGeneralCompleted =
        isDone.GeneralData &&
        isDone.MaritalData &&
        isDone.MilitaryData &&
        isDone.AddressData &&
        isDone.ContactData;

      this.isEducationCompleted =
        isDone.EducationData && isDone.TrainingData && isDone.CertificationData;

      this.isWorkExperienceCompleted =
        isDone.WorkExperincesData &&
        isDone.JobInfoData &&
        isDone.relatedData &&
        isDone.RefrenceData &&
        isDone.DocumentData;
    });
  }

  override onChangeBusinessAndCompanyId() {
    if (this.requestId > 0 && this.checkNoBusinessOrCompany()) {
      this.getRequestIdById();
    }
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((param) => {
      this.requestId = Number(param.requestId ?? 0);
      if (this.requestId > 0) {
        this.isInternal = false;
        this.candidateService.isDone$.next({
          ...this.candidateService.isDone$.getValue(),
          JobInfoData: true,
          relatedData: true,
          RefrenceData: true,
        });
      }
    });
  }

  stepChanged() {
    // prevent inputs to be invalid after next & back
    this.stepper.selected.interacted = false;
  }

  // getCandidateId(value) {
  //   this.candidateId = value;
  // }

  submit() {
    this.candidateService.submitted$.next(true);
    let isDone = this.candidateService.isDone$.getValue();
    if (Object.keys(isDone).every((key) => isDone[key] == true)) {
      let model: ICandidateGenaralRequest =
        this.candidateService.candidateData$.getValue();
      console.log(model);
      let formdata = HelpersFunctions.objectToFormData(model);
      this.saveGeneral(formdata);
    } else this.warning("pleaseEnterAllRequiredData");
  }

  saveGeneral(model: FormData) {
    this.showLoading();
    this.candidateService
      .createGeneral(model)
      .subscribe((res: IResponse<ICandidateGenaralRequest>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          if (this.isInternal) {
            this.addSuccessfullyMsg(res.message.messageTypeName);

            // // navigate to edit
            // this.candidateId = res.data.id;
            // this.formCtrls.id.setValue(this.candidateId);
            // let url = `/recruitment/${this.moduleId}/candidate/edit/${this.candidateId}`;
            // this.navigateTo(url);
          } else this.openDoneDialog();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  openDoneDialog() {
    return this.dialog
      .open(AddCandidateDoneDialogComponent, {
        autoFocus: false,
        disableClose: true,
        panelClass: "candidate-done-dialog",
      })
      .afterClosed();
  }

  getRequestIdById() {
    this.isLoaded = false;
    this.recruitmentRequestService
      .getById(this.requestId)
      .subscribe((res: IResponse<IRecruitmentRequestModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.jobTitle = this.msgTranslate(
            res.data[0].job_Title.ar_Name,
            res.data[0].job_Title.en_Name
          );
          this.requestEndData = res.data[0].request.to_Date;
          this.isLoaded = true;
        } else this.errorList(res?.message?.messages);
      });
  }
}
