import { Component, Input, OnInit } from "@angular/core";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "candidate-contact-info",
  templateUrl: "./candidate-contact-info.component.html",
  styleUrls: ["./candidate-contact-info.component.scss"],
})
export class CandidateContactInfoComponent
  extends AppHelpers
  implements OnInit
{
  @Input() candidateId: number = 0;
  @Input() isView: boolean = false;
  constructor() {
    super();
  }
  ngOnInit() {}

  descriptionInfo: ITabDescriptionInfo;
  descriptionAddressLastInfo: ITabDescriptionInfo;
  descriptionContactLastInfo: ITabDescriptionInfo;
  changeDescriptionInfo(info: ITabDescriptionInfo, tabIndex: number) {
    if (tabIndex == 1) this.descriptionAddressLastInfo = info;
    else this.descriptionContactLastInfo = info;
    this.descriptionInfo = {
      isDone:
        this.descriptionAddressLastInfo?.isDone &&
        this.descriptionContactLastInfo?.isDone,
      noOfRequiredInputs:
        this.descriptionAddressLastInfo?.noOfRequiredInputs +
        this.descriptionContactLastInfo?.noOfRequiredInputs,
    };
  }

  submit(){

  }
}
