import {
  ICandidateWorkExperienceDisplayModel,
  ICandidateWorkExperienceModel,
} from "app/recruitment/models/candidate/work-experience/work-experience";
import {
  HelpersFunctions,
  msgTranslate,
} from "app/shared/resources/helpers-functions";

export function candidateWorkExperienceToDisplay(
  data: ICandidateWorkExperienceModel[],
  resignationReasonOtherId: number
): ICandidateWorkExperienceDisplayModel[] {
  return data.map((workExperience) => {
    return <ICandidateWorkExperienceDisplayModel>{
      ...workExperience,
      company: workExperience.companyName,
      department: workExperience.org_Name,
      job: workExperience.job_Title_Name,
      startDate: HelpersFunctions.mapDate(workExperience.from_Date),
      endDate: HelpersFunctions.mapDate(workExperience.to_Date),
      resignationReason:
        resignationReasonOtherId != workExperience.resignationReasonId
          ? msgTranslate(
              workExperience.core_Resignation_Reason?.ar_Name,
              workExperience.core_Resignation_Reason?.en_Name
            )
          : workExperience.resignation_Reason,
      notes: workExperience.note,
    };
  });
}
