import { Component, OnInit } from "@angular/core";
import { MatDialogRef } from "@angular/material/dialog";

@Component({
  selector: "app-candidate-contact-dialog",
  templateUrl: "./candidate-contact-dialog.component.html",
  styleUrls: ["./candidate-contact-dialog.component.scss"],
})
export class CandidateContactDialogComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<CandidateContactDialogComponent>
  ) {}

  ngOnInit(): void {}
  onCloseDialog() {
    this.dialogRef.close();
  }
}
