import { Component, EventEmitter, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";
import { candidateContactToDisplay } from "app/recruitment/mapper/candidate/candidate-contact-info";
import {
  ICandidateContactDisplayModel,
  ICandidateContactModel,
  ICandidateContactRequest,
} from "app/recruitment/models/candidate/general-info/contacts";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { IContactTypeModel } from "app/system-setup/hr-setup/models/contact-type";

@Component({
  selector: "candidate-contacts-form",
  templateUrl: "./candidate-contacts-form.component.html",
  styleUrls: ["./candidate-contacts-form.component.scss"],
})
export class CandidateContactsFormComponent extends AppHelpers implements OnInit {
  // @Input() candidateId: number = 0;
  @Output() changeDescriptionInfo = new EventEmitter<ITabDescriptionInfo>();

  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    // "Ser",
    "contactType",
    "value",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<ICandidateContactDisplayModel>;

  selectedContactType: IContactTypeModel;

  constructor(private candidateService: CandidateService) {
    super();

    this.dataSource = new MatTableDataSource([]);
    this.candidateService.submitted$.subscribe((submitted) => {
      if (this.dataSource.data.length == 0) this.submitted = submitted;
    });
  }

  // override onChangeBusinessAndCompanyId() {
  //   if (this.dataSource?.data?.length > 0)
  //     this.dataSource = new MatTableDataSource(null);

  //   if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
  //     this.getAll();
  //   } else this.isLoaded = true;
  // }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      contacT_TYPE_ID: [null, [Validators.required]],
      value: [null, [Validators.required]],
    });

    this.invalidRequiredCount();
    this.isLoaded = true;
  }

  // ngOnChanges(changes: SimpleChanges): void {
  // if (
  //   changes.candidateId?.currentValue > 0 &&
  //   this.checkNoBusinessOrCompany()
  // )
  //   this.getAll();
  // }

  get formCtrls() {
    return this.form.controls;
  }

  // getAll() {
  //   this.dataSource = new MatTableDataSource(null);
  //   this.isLoaded = false;
  //   this.candidateContactsService
  //     .getAll(this.candidateId)
  //     .subscribe((res: IResponse<ICandidateContactModel[]>) => {
  //       if ((res.message.messageType = MessageTypes.Success)) {
  //         this.dataSource = new MatTableDataSource(
  //           candidateContactToDisplay(res.data)
  //         );
  //         if (res.data.length > 0) this.changeDescription(true, 0);
  //         else this.invalidRequiredCount();
  //         this.isLoaded = true;
  //       } else this.errorList(res.message.messages);
  //     });
  // }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ICandidateContactRequest = {
        // person_ID: this.candidateId,
        comm_Contact: {
          // id: this.form.value.id,
          id: this.form.value.id == 0 ? this.getNewId() : this.form.value.id,
          contacT_TYPE_ID: this.form.value.contacT_TYPE_ID,
          value: this.form.value.value,
          isVaild: true,
          isShow: true,
        },
      };

      const indexModel: ICandidateContactModel = {
        comm_Contact: {
          ...model.comm_Contact,
          comm_Contact_Type: this.selectedContactType,
        },
      };

      this.form.value.id == 0
        ? this.create(indexModel)
        : this.update(indexModel);
    }
    // else this.toastr.error("General.formNotValid");
  }

  create(model: ICandidateContactModel) {
    // this.showLoading();
    // this.candidateContactsService
    //   .create(model)
    //   .subscribe((res: IResponse<ICandidateContactModel>) => {
    //     if (res?.message?.messageType == MessageTypes.Success) {
    //       this.addSuccessfullyMsg(res?.message?.messageTypeName);
    //       this.clearForm();
    //       this.getAll();
    //     } else this.errorList(res.message.messages);
    //     this.hideLoading();
    //   });

    this.dataSource.data.push(candidateContactToDisplay([model])[0]);
    this.clearForm();
    this.invalidRequiredCount();
    this.updateGeneralData();
  }

  update(model: ICandidateContactModel) {
    // this.showLoading();
    // this.candidateContactsService
    //   .update(model)
    //   .subscribe((res: IResponse<ICandidateContactModel>) => {
    //     if (res?.message?.messageType == MessageTypes.Success) {
    //       this.updateSuccessfullyMsg(res?.message?.messageTypeName);
    //       this.clearForm();
    //       this.getAll();
    //     } else this.errorList(res.message.messages);
    //     this.hideLoading();
    //   });

    let mapedModel: ICandidateContactDisplayModel = candidateContactToDisplay([
      model,
    ])[0];
    this.dataSource.data[this.getIndex(mapedModel)] = mapedModel;
    this.clearForm();
    this.updateGeneralData();
  }

  candidateContactToEdit(contact: ICandidateContactDisplayModel) {
    this.form.patchValue(contact.comm_Contact);
  }

  onDelete(contact: ICandidateContactDisplayModel): void {
    let msg = contact.contactType + " - " + contact.value;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(contact);
    });
  }

  delete(contact: ICandidateContactDisplayModel) {
    // let deleteParams: ICandidateContactDeleteParams = {
    //   person_ID: this.candidateId,
    //   contact_ID: contact.contact_ID,
    // };
    // this.showLoading();
    // this.candidateContactsService
    //   .Delete(deleteParams)
    //   .subscribe((res: IResponse) => {
    //     if (res.message.messageType == MessageTypes.Success) {
    //       this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
    //       this.getAll();
    //       if (this.formCtrls.id.value == contact.comm_Contact.id)
    //         this.clearForm();
    //     } else this.errorList(res?.message?.messages);
    //     this.hideLoading();
    //   });

    this.dataSource.data.splice(this.getIndex(contact), 1);
    this.invalidRequiredCount();
    if (this.formCtrls.id.value == contact.comm_Contact.id) this.clearForm();
    this.updateGeneralData();
  }

  updateGeneralData() {
    let generalModel: ICandidateContactRequest[] = [];
    this.dataSource.data.forEach((ele) => {
      const { id, comm_Contact_Type, ...contact } = ele.comm_Contact;
      generalModel.push({ comm_Contact: contact });
    });
    this.candidateService.candidateData$.next({
      ...this.candidateService.candidateData$.getValue(),
      ContactData: generalModel,
    });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getIndex(contact: ICandidateContactDisplayModel): number {
    return this.dataSource.data.findIndex(
      (ele) => ele.comm_Contact.id == contact.comm_Contact.id
    );
  }

  getNewId(): number {
    return (
      this.dataSource.data.reduce(
        (acc, ele) =>
          (acc = acc > ele.comm_Contact.id ? acc : ele.comm_Contact.id),
        0
      ) + 1
    );
  }

  onChangeContactType(contact: IContactTypeModel[]) {
    if (contact.length > 0) {
      this.formCtrls.value.setValidators([
        Validators.required,
        Validators.pattern(contact[0]?.regularExpression),
      ]);
      this.selectedContactType = contact[0];
    } else {
      this.formCtrls.value.setValidators([Validators.required]);
      this.selectedContactType = null;
    }
    this.formCtrls.value.updateValueAndValidity();

    this.invalidRequiredCount();
  }

  changeDescription(isDone: boolean, noOfRequiredInputs: number) {
    this.changeDescriptionInfo.emit({
      isDone: isDone,
      noOfRequiredInputs: noOfRequiredInputs,
    });

    this.candidateService.isDone$.next({
      ...this.candidateService.isDone$.getValue(),
      ContactData: this.dataSource?.data?.length > 0 ? true : false,
    });
  }

  invalidRequiredCount() {
    if (!(this.dataSource?.data?.length > 0)) {
      let count = HelpersFunctions.noOfInvalidAndRequiredInputs(this.form);
      this.changeDescription(false, count);
    } else this.changeDescription(true, 0);
  }
}
