import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";
import { candidateReferenceToDisplay } from "app/recruitment/mapper/candidate/candidate-reference";
import {
  ICandidateReferenceDisplayModel,
  ICandidateReferenceRequest,
} from "app/recruitment/models/candidate/work-experience/reference";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "candidate-reference-form",
  templateUrl: "./candidate-reference-form.component.html",
  styleUrls: ["./candidate-reference-form.component.scss"],
})
export class CandidateReferenceFormComponent extends AppHelpers implements OnInit {
  @Input() candidateId: number = 0;
  @Output() changeDescription = new EventEmitter<ITabDescriptionInfo>();
  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    // "Ser",
    "company",
    "reference",
    "position",
    "phone",
    "email",
    "notes",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<ICandidateReferenceDisplayModel>;

  constructor(private candidateService: CandidateService) {
    super();

    this.dataSource = new MatTableDataSource([]);
    this.candidateService.submitted$.subscribe((submitted) => {
      if (this.dataSource.data.length == 0) this.submitted = submitted;
    });
  }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      company_Name: [null, [Validators.required]],
      refrence_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      position: [null],
      phone: [null, CustomValidator.mobileValidators],
      email: [null, [CustomValidators.email]],
      note: [null],
    });

    this.dataSource = new MatTableDataSource([]);
    this.invalidRequiredCount();
    this.isLoaded = true;
  }

  get formCtrls() {
    return this.form.controls;
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ICandidateReferenceRequest = {
        // person_Instance_ID: this.candidateId,
        id: this.form.value.id == 0 ? this.getNewId() : this.form.value.id,
        company_Name: this.form.value.company_Name,
        refrence_Name: this.form.value.refrence_Name,
        position: this.form.value.position,
        phone: this.form.value.phone,
        email: this.form.value.email,
        note: this.form.value.note,
      };

      this.form.value.id == 0 ? this.create(model) : this.update(model);
    }
    // else this.toastr.error("General.formNotValid");
  }

  create(model: ICandidateReferenceRequest) {
    this.dataSource.data.push(candidateReferenceToDisplay([model])[0]);
    this.clearForm();
    this.invalidRequiredCount();
    this.updateGeneralData();
  }

  update(model: ICandidateReferenceRequest) {
    let mapedModel: ICandidateReferenceDisplayModel =
      candidateReferenceToDisplay([model])[0];
    this.dataSource.data[this.getIndex(mapedModel)] = mapedModel;
    this.clearForm();
    this.updateGeneralData();
  }

  candidateReferenceToEdit(reference: ICandidateReferenceDisplayModel) {
    this.form.patchValue(reference);
  }

  onDelete(reference: ICandidateReferenceDisplayModel): void {
    let msg = `${reference.company} - ${reference.reference}`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(reference);
    });
  }

  delete(reference: ICandidateReferenceDisplayModel) {
    this.dataSource.data.splice(this.getIndex(reference), 1);
    this.invalidRequiredCount();
    if (reference.id == this.formCtrls.id.value) this.clearForm();
    this.updateGeneralData();
  }

  updateGeneralData() {
    let generalModel: ICandidateReferenceRequest[] = [];
    this.dataSource.data.forEach((ele) => {
      let reference = { ...ele };
      delete reference.id;
      delete reference.reference;
      delete reference.company;
      delete reference.mobileNumber;
      delete reference.notes;
      generalModel.push(reference);
    });
    this.candidateService.candidateData$.next({
      ...this.candidateService.candidateData$.getValue(),
      RefrenceData: generalModel,
    });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getIndex(reference: ICandidateReferenceDisplayModel): number {
    return this.dataSource.data.findIndex((ele) => ele.id == reference.id);
  }

  getNewId(): number {
    return (
      this.dataSource.data.reduce(
        (acc, ele) => (acc = acc > ele.id ? acc : ele.id),
        0
      ) + 1
    );
  }

  descriptionInfo: ITabDescriptionInfo;
  changeDescriptionInfo(isDone: boolean, noOfRequiredInputs: number) {
    this.changeDescription.emit({
      isDone: isDone,
      noOfRequiredInputs: noOfRequiredInputs,
    });

    this.candidateService.isDone$.next({
      ...this.candidateService.isDone$.getValue(),
      // RefrenceData: this.descriptionInfo.isDone,
      RefrenceData:isDone,

    });
  }

  invalidRequiredCount() {
    if (!(this.dataSource?.data?.length > 0)) {
      let count = HelpersFunctions.noOfInvalidAndRequiredInputs(this.form);
      this.changeDescriptionInfo(false, count);
    } else this.changeDescriptionInfo(true, 0);
  }
}
