import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";
import { candidateRelatedRequestsToDisplay } from "app/recruitment/mapper/candidate/candidate-related-requests";
import {
  ICandidateRelatedRequestsDisplayModel,
  ICandidateRelatedRequestsModel,
  ICandidateRelatedRequestsRequest,
} from "app/recruitment/models/candidate/work-experience/related-requests";
import { IRecruitmentPlanModel } from "app/recruitment/models/plan/recruitment-plan";
import { IRecruitmentRequestModel } from "app/recruitment/models/recruitment-request";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";

@Component({
  selector: "candidate-related-requests-form",
  templateUrl: "./candidate-related-requests-form.component.html",
  styleUrls: ["./candidate-related-requests-form.component.scss"],
})
export class CandidateRelatedFormRequestsComponent
  extends AppHelpers
  implements OnInit
{
  @Input() candidateId: number = 0;
  @Output() changeDescription = new EventEmitter<ITabDescriptionInfo>();

  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    // "Ser",
    "plan",
    "request",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<ICandidateRelatedRequestsDisplayModel>;

  selectedPlan: IRecruitmentPlanModel;
  selectedRequest: IRecruitmentRequestModel;

  constructor(private candidateService: CandidateService) {
    super();

    this.dataSource = new MatTableDataSource([]);
    this.candidateService.submitted$.subscribe((submitted) => {
      if (this.dataSource.data.length == 0) this.submitted = submitted;
    });
  }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      planId: [null, [Validators.required]],
      request_ID: [null, [Validators.required]],
    });

    this.dataSource = new MatTableDataSource([]);
    this.invalidRequiredCount();
    this.isLoaded = true;
  }

  get formCtrls() {
    return this.form.controls;
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ICandidateRelatedRequestsRequest = {
        // person_Instance_ID: this.candidateId,
        id: this.form.value.id == 0 ? this.getNewId() : this.form.value.id,
        request_ID: this.form.value.request_ID,
      };

      const indexModel: ICandidateRelatedRequestsModel = {
        ...model,
        planId: this.form.value.planId,
        rec_Plan: this.selectedPlan,
        rec_Request: this.selectedRequest,
      };

      this.form.value.id == 0
        ? this.create(indexModel)
        : this.update(indexModel);
    }
    // else this.toastr.error("General.formNotValid");
  }

  create(model: ICandidateRelatedRequestsModel) {
    this.dataSource.data.push(candidateRelatedRequestsToDisplay([model])[0]);
    this.clearForm();
    this.invalidRequiredCount();
    this.updateGeneralData();
  }

  update(model: ICandidateRelatedRequestsModel) {
    let mapedModel: ICandidateRelatedRequestsDisplayModel =
      candidateRelatedRequestsToDisplay([model])[0];
    this.dataSource.data[this.getIndex(mapedModel)] = mapedModel;
    this.clearForm();
    this.updateGeneralData();
  }

  candidateRelatedRequestsToEdit(
    relatedRequests: ICandidateRelatedRequestsDisplayModel
  ) {
    this.form.patchValue(relatedRequests);
    this.formCtrls.request_ID.setValue(relatedRequests.request_ID);
  }

  onDelete(relatedRequests: ICandidateRelatedRequestsDisplayModel): void {
    let msg = `${relatedRequests.plan} - ${relatedRequests.request}`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(relatedRequests);
    });
  }

  delete(relatedRequests: ICandidateRelatedRequestsDisplayModel) {
    this.dataSource.data.splice(this.getIndex(relatedRequests), 1);
    this.invalidRequiredCount();
    if (relatedRequests.id == this.formCtrls.id.value) this.clearForm();
    this.updateGeneralData();
  }

  updateGeneralData() {
    let generalModel: ICandidateRelatedRequestsRequest[] = [];
    this.dataSource.data.forEach((ele) => {
      let relatedRequests = { ...ele };
      delete relatedRequests.id;
      delete relatedRequests.planId;
      delete relatedRequests.request;
      delete relatedRequests.plan;
      delete relatedRequests.rec_Plan;
      delete relatedRequests.rec_Request;
      generalModel.push(relatedRequests);
    });
    this.candidateService.candidateData$.next({
      ...this.candidateService.candidateData$.getValue(),
      relatedData: generalModel,
    });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getIndex(relatedRequests: ICandidateRelatedRequestsDisplayModel): number {
    return this.dataSource.data.findIndex(
      (ele) => ele.id == relatedRequests.id
    );
  }

  getNewId(): number {
    return (
      this.dataSource.data.reduce(
        (acc, ele) => (acc = acc > ele.id ? acc : ele.id),
        0
      ) + 1
    );
  }

  onChangePlan(values: IRecruitmentPlanModel[]) {
    this.selectedPlan = values.length > 0 ? values[0] : null;
    this.invalidRequiredCount();
  }

  onChangeRequest(values: IRecruitmentRequestModel[]) {
    this.selectedRequest =
      values?.length > 0 ? values[0] : this.selectedRequest;
    this.invalidRequiredCount();
  }

  descriptionInfo: ITabDescriptionInfo;
  changeDescriptionInfo(isDone: boolean, noOfRequiredInputs: number) {
    this.changeDescription.emit({
      isDone: isDone,
      noOfRequiredInputs: noOfRequiredInputs,
    });

    this.candidateService.isDone$.next({
      ...this.candidateService.isDone$.getValue(),
      // relatedData: this.descriptionInfo.isDone,
      relatedData:isDone
    });
  }

  invalidRequiredCount() {
    if (!(this.dataSource?.data?.length > 0)) {
      let count = HelpersFunctions.noOfInvalidAndRequiredInputs(this.form);
      this.changeDescriptionInfo(count == 0 ? true : false, count);
    } else this.changeDescriptionInfo(true, 0);
  }
}
