import { Component, EventEmitter, OnInit, Output} from '@angular/core';
import { IGUIModel } from 'app/shared/interfaces/user-views';
import { AppHelpers } from 'app/shared/resources/app-helpers';
import { ViewsManager } from 'app/shared/resources/views-manager';
import { CandidateWorkExperienceDialogComponent } from '../candidate-work-experience-dialog/candidate-work-experience-dialog.component';
@Component({
  selector: 'candidate-work-experience-card',
  templateUrl: './candidate-work-experience-card.component.html',
  styleUrls: ['./candidate-work-experience-card.component.scss']
})
export class CandidateWorkExperienceCardComponent extends AppHelpers  implements OnInit {
  isLoaded: boolean = true;
  allowedSections: IGUIModel[];
  @Output() onClickIcon? = new EventEmitter<any>();
  isTableView: boolean = false;
  candidateexperienceId: number = 2;


  constructor() {
    super();
  }
  ngOnInit(): void {
  }
  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.candidate_Working_Experience_info_sec
    );

  }

  openEditDialog(){

    let dialogRef = this.dialogsService.addEditDialog(
      CandidateWorkExperienceDialogComponent
    );
  }
  click($event) {
    this.onClickIcon.emit($event);
  }
  toggleTable() {
    this.isTableView = !this.isTableView;
  }

}
