import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { candidateEducationToDisplay } from "app/recruitment/mapper/candidate/candidate-education";
import {
  ICandidateEducationDisplayModel,
  ICandidateEducationModel,
  ICandidateEducationRequest,
} from "app/recruitment/models/candidate/education/education";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { IEducationDegreeModel } from "app/system-setup/hr-setup/models/education-degree";
import { IInstitutionModel } from "app/system-setup/hr-setup/models/institution";
import { ITabDescriptionInfo } from "../../../shared-candidate-components/candidate-tab-description/candidate-tab-description.component";

@Component({
  selector: "candidate-education-form",
  templateUrl: "./candidate-education-form.component.html",
  styleUrls: ["./candidate-education-form.component.scss"],
})
export class CandidateEducationFormComponent
  extends AppHelpers
  implements OnInit
{
  @Input() candidateId: number = 0;
  @Output() changeDescription = new EventEmitter<ITabDescriptionInfo>();
  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    // "Ser",
    "institution",
    "academicQualifications",
    "major",
    "year",
    "grade",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<ICandidateEducationDisplayModel>;
  selectedEducation: ICandidateEducationDisplayModel;

  selectedInstitution: IInstitutionModel;
  selectedAcademicQualifications: IEducationDegreeModel;
  selectedGrade: Idropdown;

  constructor(private candidateService: CandidateService) {
    super();

    this.dataSource = new MatTableDataSource([]);
    this.candidateService.submitted$.subscribe((submitted) => {
      if (this.dataSource.data.length == 0) this.submitted = submitted;
    });
  }

  // override onChangeBusinessAndCompanyId() {
  //   if (this.dataSource?.data?.length > 0)
  //     this.dataSource = new MatTableDataSource(null);

  //   if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
  //     this.getAll();
  //   } else this.isLoaded = true;
  // }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      institutionID: [null, [Validators.required]],
      academicID: [null, [Validators.required]],
      major: [null],
      gradeID: [null, [Validators.required]],
      year: [null, [Validators.required]],
    });

    this.dataSource = new MatTableDataSource([]);
    this.invalidRequiredCount();
    this.isLoaded = true;
  }

  get formCtrls() {
    return this.form.controls;
  }

  // getAll() {
  //   this.dataSource = new MatTableDataSource(null);
  //   this.isLoaded = false;
  //   this.candidateEducationService
  //     .getAll(this.candidateId)
  //     .subscribe((res: IResponse<ICandidateEducationModel[]>) => {
  //       if ((res.message.messageType = MessageTypes.Success)) {
  //         this.dataSource = new MatTableDataSource(
  //           candidateEducationToDisplay(res.data)
  //         );
  //         if (res.data.length > 0) this.changeDescriptionInfo(true, 0);
  //         else this.invalidRequiredCount();
  //         this.isLoaded = true;
  //       } else this.errorList(res.message.messages);
  //     });
  // }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ICandidateEducationRequest = {
        // personID: this.candidateId,
        id: this.form.value.id == 0 ? this.getNewId() : this.form.value.id,
        institutionID: this.form.value.institutionID,
        academicID: this.form.value.academicID,
        major: this.form.value.major,
        gradeID: this.form.value.gradeID,
        year: this.form.value.year,
      };

      const indexModel: ICandidateEducationModel = {
        ...model,
        comm_Institution: this.selectedInstitution,
        comm_Education_Degree: this.selectedAcademicQualifications,
        comm_Grade_Type: this.selectedGrade,
      };

      this.form.value.id == 0
        ? this.create(indexModel)
        : this.update(indexModel);
    }
    // else this.toastr.error("General.formNotValid");
  }

  create(model: ICandidateEducationModel) {
    // this.showLoading();
    // this.candidateEducationService
    //   .create(model)
    //   .subscribe((res: IResponse<ICandidateEducationModel>) => {
    //     if (res?.message?.messageType == MessageTypes.Success) {
    //       this.addSuccessfullyMsg(res?.message?.messageTypeName);
    //       this.clearForm();
    //       this.getAll();
    //     } else this.errorList(res.message.messages);
    //     this.hideLoading();
    //   });

    this.dataSource.data.push(candidateEducationToDisplay([model])[0]);
    this.clearForm();
    this.invalidRequiredCount();
    this.updateGeneralData();
  }

  update(model: ICandidateEducationModel) {
    // let editModel: ICandidateEducationEditRequest = {
    //   OriginalEducation: {
    //     personID: this.candidateId,
    //     institutionID: this.selectedEducation.institutionID,
    //     academicID: this.selectedEducation.academicID,
    //     major: this.selectedEducation.major,
    //     gradeID: this.selectedEducation.gradeID,
    //     year: this.selectedEducation.year,
    //   },
    //   Education: model,
    // };
    // this.showLoading();
    // this.candidateEducationService
    //   .update(editModel)
    //   .subscribe((res: IResponse<ICandidateEducationModel>) => {
    //     if (res?.message?.messageType == MessageTypes.Success) {
    //       this.updateSuccessfullyMsg(res?.message?.messageTypeName);
    //       this.clearForm();
    //       this.getAll();
    //       this.selectedEducation = null;
    //     } else this.errorList(res.message.messages);
    //     this.hideLoading();
    //   });

    let mapedModel: ICandidateEducationDisplayModel =
      candidateEducationToDisplay([model])[0];
    this.dataSource.data[this.getIndex(mapedModel)] = mapedModel;
    this.clearForm();
    this.updateGeneralData();
  }

  candidateeEducationToEdit(education: ICandidateEducationDisplayModel) {
    this.form.patchValue(education);
    // this.selectedEducation = education;
    // // to detect edit mode
    // this.formCtrls.id.setValue(1);
  }

  onDelete(education: ICandidateEducationDisplayModel): void {
    let msg = education.academicQualifications + " - " + education.grade;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(education);
    });
  }

  delete(education: ICandidateEducationDisplayModel) {
    // let deleteParams: ICandidateEducationDeleteParams = {
    //   PersonID: this.candidateId,
    //   AcademicID: education.academicID,
    //   GradeID: education.gradeID,
    // };
    // this.showLoading();
    // this.candidateEducationService
    //   .Delete(deleteParams)
    //   .subscribe((res: IResponse) => {
    //     if (res.message.messageType == MessageTypes.Success) {
    //       this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
    //       this.getAll();
    //       if (
    //         education.academicID == this.selectedEducation?.academicID &&
    //         education.gradeID == this.selectedEducation?.gradeID &&
    //         this.formCtrls.id.value > 0
    //       )
    //         this.clearForm();
    //     } else this.errorList(res?.message?.messages);
    //     this.hideLoading();
    //   });

    this.dataSource.data.splice(this.getIndex(education), 1);
    this.invalidRequiredCount();
    if (this.formCtrls.id.value == education.id) this.clearForm();
    this.updateGeneralData();
  }

  updateGeneralData() {
    let generalModel: ICandidateEducationRequest[] = [];
    this.dataSource.data.forEach((ele) => {
      let education = { ...ele };
      delete education.id;
      delete education.institution;
      delete education.academicQualifications;
      delete education.grade;
      delete education.comm_Institution;
      delete education.comm_Education_Degree;
      delete education.comm_Grade_Type;
      generalModel.push(education);
    });
    this.candidateService.candidateData$.next({
      ...this.candidateService.candidateData$.getValue(),
      EducationData: generalModel,
    });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getIndex(education: ICandidateEducationDisplayModel): number {
    return this.dataSource.data.findIndex((ele) => ele.id == education.id);
  }

  getNewId(): number {
    return (
      this.dataSource.data.reduce(
        (acc, ele) => (acc = acc > ele.id ? acc : ele.id),
        0
      ) + 1
    );
  }

  onChangeInstitution(values: IInstitutionModel[]) {
    this.selectedInstitution = values.length > 0 ? values[0] : null;
    this.invalidRequiredCount();
  }

  onChangeAcademicQualifications(values: IEducationDegreeModel[]) {
    this.selectedAcademicQualifications = values.length > 0 ? values[0] : null;
    this.invalidRequiredCount();
  }

  onChangeGrade(values: Idropdown[]) {
    this.selectedGrade = values.length > 0 ? values[0] : null;
    this.invalidRequiredCount();
  }

  descriptionInfo: ITabDescriptionInfo;
  changeDescriptionInfo(isDone: boolean, noOfRequiredInputs: number) {
    this.changeDescription.emit({
      isDone: isDone,
      noOfRequiredInputs: noOfRequiredInputs,
    });

    this.candidateService.isDone$.next({
      ...this.candidateService.isDone$.getValue(),
      EducationData: isDone,
    });
  }

  invalidRequiredCount() {
    if (!(this.dataSource?.data?.length > 0)) {
      let count = HelpersFunctions.noOfInvalidAndRequiredInputs(this.form);
      this.changeDescriptionInfo(count == 0 ? true : false, count);
    } else this.changeDescriptionInfo(true, 0);
  }
}
