import {
  ICandidateEducationDisplayModel,
  ICandidateEducationModel,
} from "app/recruitment/models/candidate/education/education";
import { msgTranslate } from "app/shared/resources/helpers-functions";

export function candidateEducationToDisplay(
  data: ICandidateEducationModel[]
): ICandidateEducationDisplayModel[] {
  return data.map((education) => {
    return <ICandidateEducationDisplayModel>{
      ...education,
      institution: msgTranslate(
        education.comm_Institution?.ar_Name,
        education.comm_Institution?.en_Name
      ),
      academicQualifications: msgTranslate(
        education.comm_Education_Degree?.ar_Name,
        education.comm_Education_Degree?.en_Name
      ),
      grade: msgTranslate(
        education.comm_Grade_Type?.ar_Name,
        education.comm_Grade_Type?.en_Name
      ),
    };
  });
}
