import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { AppHelpers } from 'app/shared/resources/app-helpers';
import { ViewsManager } from 'app/shared/resources/views-manager';
@Component({
  selector: 'candidate-certification-table',
  templateUrl: './candidate-certification-table.component.html',
  styleUrls: ['./candidate-certification-table.component.scss']
})
export class CandidateCertificationTableComponent  extends AppHelpers  implements OnInit {

  @Input() isMatPaginator: boolean = false;
  @Output() afterDelete = new EventEmitter<any>;
  dataSource: MatTableDataSource<any>;

  mandatoryColomns: string[] = [
    "certification",
    "location",
    "start_date",
    "end_date",
    "notes",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean;
  @Input() hideActions: boolean = false;

  constructor() {
    super();

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.candidate_certification_info_sec
    );
  }

  ngOnInit(): void {
  }
  getList(page: number, pageSize?: number): void {
  }
  onDelete(contactInfo): void {
    let msg = this.msgTranslate(contactInfo.ar_Name,contactInfo.en_Name )
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(contactInfo);
    });
  }

  delete(contact) {

    this.showLoading('table');

  }


}
