import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-candidate-training-dialog',
  templateUrl: './candidate-training-dialog.component.html',
  styleUrls: ['./candidate-training-dialog.component.scss']
})
export class CandidateTrainingDialogComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<CandidateTrainingDialogComponent>
  ) {}

  ngOnInit(): void {}

  onCloseDialog() {
    this.dialogRef.close();
  }


}
