import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { IGUIModel } from 'app/shared/interfaces/user-views';
import { AppHelpers } from 'app/shared/resources/app-helpers';
import { ViewsManager } from 'app/shared/resources/views-manager';
import { CandidateBasicInfoDialogComponent } from '../candidate-basic-info-dialog/candidate-basic-info-dialog.component';

@Component({
  selector: 'candidate-basic-info-card',
  templateUrl: './candidate-basic-info-card.component.html',
  styleUrls: ['./candidate-basic-info-card.component.scss']
})
export class CandidateBasicInfoCardComponent extends AppHelpers  implements OnInit {
  isLoaded: boolean = true;
  allowedSections: IGUIModel[];
  @Output() onClickIcon? = new EventEmitter<any>();


  constructor() {
    super();
  }
  ngOnInit(): void {
  }
  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.candidate_basic_info_sec
    );

  }

  openEditDialog(){

    let dialogRef = this.dialogsService.addEditDialog(
      CandidateBasicInfoDialogComponent
    );
  }
  click($event) {
    this.onClickIcon.emit($event);
  }
}
