  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      attachmentTypeID: [null, [Validators.required]],
      attachment: [null, [Validators.required]],
    });

    this.dataSource = new MatTableDataSource([]);
    this.invalidRequiredCount();
    this.isLoaded = true;
    this.getDocumentRowFunctions();
  }

  override onModulesChanged() {
    this.getDocumentRowFunctions();
  }

  getDocumentRowFunctions() {
    this.rowFunctions?.basic.forEach((e) => {
      if (e.name.toLowerCase() == "delete")
        this.documentsRowFunctions?.basic.push(e);
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

