import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'candidate-reference-dialog',
  templateUrl: './candidate-reference-dialog.component.html',
  styleUrls: ['./candidate-reference-dialog.component.scss']
})
export class CandidateReferenceDialogComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<CandidateReferenceDialogComponent>
  ) {}

  ngOnInit(): void {}

  onCloseDialog() {
    this.dialogRef.close();
  }


}
