import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'candidate-certification-dialog',
  templateUrl: './candidate-certification-dialog.component.html',
  styleUrls: ['./candidate-certification-dialog.component.scss']
})
export class CandidateCertificationDialogComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<CandidateCertificationDialogComponent>
  ) {}

  ngOnInit(): void {}

  onCloseDialog() {
    this.dialogRef.close();
  }

}
