import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";
import { candidateCertificationToDisplay } from "app/recruitment/mapper/candidate/candidate-certification";
import {
  ICandidateCertificationDisplayModel,
  ICandidateCertificationRequest,
} from "app/recruitment/models/candidate/education/certifications";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "candidate-certification-form",
  templateUrl: "./candidate-certifications-form.component.html",
  styleUrls: ["./candidate-certifications-form.component.scss"],
})
export class CandidateCertificationFormComponent
  extends AppHelpers
  implements OnInit
{
  @Input() candidateId: number = 0;
  @Output() changeDescription = new EventEmitter<ITabDescriptionInfo>();
  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    // "Ser",
    "certification",
    "location",
    "startDate",
    "endDate",
    "notes",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<ICandidateCertificationDisplayModel>;

  constructor(private candidateService: CandidateService) {
    super();

    this.dataSource = new MatTableDataSource([]);
    this.candidateService.submitted$.subscribe((submitted) => {
      if (this.dataSource.data.length == 0) this.submitted = submitted;
    });
  }

  // override onChangeBusinessAndCompanyId() {
  //   if (this.dataSource?.data?.length > 0)
  //     this.dataSource = new MatTableDataSource(null);

  //   if (this.candidateId > 0 && this.checkNoBusinessOrCompany()) {
  //     this.getAll();
  //   } else this.isLoaded = true;
  // }

  ngOnInit() {
    this.form = this.fb.group(
      {
        id: [0, { nonNullable: true }],
        certification_Name: [null /* , [Validators.required] */],
        location_Name: [null /* , [Validators.required] */],
        from_Date: [null, [Validators.required]],
        to_Date: [null, [Validators.required]],
        note: [null],
      },
      {
        validator: CustomValidator.compareDates("from_Date", "to_Date"),
      }
    );

    this.dataSource = new MatTableDataSource([]);
    this.invalidRequiredCount();
    this.isLoaded = true;
  }

  get formCtrls() {
    return this.form.controls;
  }

  // getAll() {
  //   this.dataSource = new MatTableDataSource(null);
  //   this.isLoaded = false;
  //   this.candidateCertificationService
  //     .getAll(this.candidateId)
  //     .subscribe((res: IResponse<ICandidateCertificationModel[]>) => {
  //       if ((res.message.messageType = MessageTypes.Success)) {
  //         this.dataSource = new MatTableDataSource(
  //           candidateCertificationToDisplay(res.data)
  //         );
  //         if (res.data.length > 0) this.changeDescriptionInfo(true, 0);
  //         else this.invalidRequiredCount();
  //         this.isLoaded = true;
  //       } else this.errorList(res.message.messages);
  //     });
  // }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ICandidateCertificationRequest = {
        // person_Instance_ID: this.candidateId,
        id: this.form.value.id == 0 ? this.getNewId() : this.form.value.id,
        certification_Name: this.form.value.certification_Name,
        location_Name: this.form.value.location_Name,
        from_Date: HelpersFunctions.mapDateReverse(this.form.value.from_Date),
        to_Date: HelpersFunctions.mapDateReverse(this.form.value.to_Date),
        note: this.form.value.note,
      };

      this.form.value.id == 0 ? this.create(model) : this.update(model);
    }
    // else this.toastr.error("General.formNotValid");
  }

  create(model: ICandidateCertificationRequest) {
    // this.showLoading();
    // this.candidateCertificationService
    //   .create(model)
    //   .subscribe((res: IResponse<ICandidateCertificationModel>) => {
    //     if (res?.message?.messageType == MessageTypes.Success) {
    //       this.addSuccessfullyMsg(res?.message?.messageTypeName);
    //       this.clearForm();
    //       this.getAll();
    //     } else this.errorList(res.message.messages);
    //     this.hideLoading();
    //   });

    this.dataSource.data.push(candidateCertificationToDisplay([model])[0]);
    this.clearForm();
    this.invalidRequiredCount();
    this.updateGeneralData();
  }

  update(model: ICandidateCertificationRequest) {
    // this.showLoading();
    // this.candidateCertificationService
    //   .update(model)
    //   .subscribe((res: IResponse<ICandidateCertificationModel>) => {
    //     if (res?.message?.messageType == MessageTypes.Success) {
    //       this.updateSuccessfullyMsg(res?.message?.messageTypeName);
    //       this.clearForm();
    //       this.getAll();
    //     } else this.errorList(res.message.messages);
    //     this.hideLoading();
    //   });

    let mapedModel: ICandidateCertificationDisplayModel =
      candidateCertificationToDisplay([model])[0];
    this.dataSource.data[this.getIndex(mapedModel)] = mapedModel;
    this.clearForm();
    this.updateGeneralData();
  }

  candidateCertificationToEdit(
    certification: ICandidateCertificationDisplayModel
  ) {
    this.form.patchValue(certification);
  }

  onDelete(certification: ICandidateCertificationDisplayModel): void {
    let msg = `${certification.certification} (${certification.startDate} - ${certification.endDate})`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(certification);
    });
  }

  delete(certification: ICandidateCertificationDisplayModel) {
    // this.showLoading();
    // this.candidateCertificationService
    //   .Delete(certification.id)
    //   .subscribe((res: IResponse) => {
    //     if (res.message.messageType == MessageTypes.Success) {
    //       this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
    //       this.getAll();
    //       if (certification.id == this.formCtrls.id.value) this.clearForm();
    //     } else this.errorList(res?.message?.messages);
    //     this.hideLoading();
    //   });

    this.dataSource.data.splice(this.getIndex(certification), 1);
    this.invalidRequiredCount();
    if (certification.id == this.formCtrls.id.value) this.clearForm();
    this.updateGeneralData();
  }

  updateGeneralData() {
    let generalModel: ICandidateCertificationRequest[] = [];
    this.dataSource.data.forEach((ele) => {
      let certification = { ...ele };
      delete certification.id;
      delete certification.certification;
      delete certification.location;
      delete certification.startDate;
      delete certification.endDate;
      delete certification.notes;
      generalModel.push(certification);
    });
    this.candidateService.candidateData$.next({
      ...this.candidateService.candidateData$.getValue(),
      CertificationData: generalModel,
    });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getIndex(certification: ICandidateCertificationDisplayModel): number {
    return this.dataSource.data.findIndex((ele) => ele.id == certification.id);
  }

  getNewId(): number {
    return (
      this.dataSource.data.reduce(
        (acc, ele) => (acc = acc > ele.id ? acc : ele.id),
        0
      ) + 1
    );
  }

  descriptionInfo: ITabDescriptionInfo;
  changeDescriptionInfo(isDone: boolean, noOfRequiredInputs: number) {
    this.changeDescription.emit({
      isDone: isDone,
      noOfRequiredInputs: noOfRequiredInputs,
    });

    this.candidateService.isDone$.next({
      ...this.candidateService.isDone$.getValue(),
      // CertificationData: this.descriptionInfo.isDone,
      CertificationData: isDone,

    });
  }

  invalidRequiredCount() {
    if (!(this.dataSource?.data?.length > 0)) {
      let count = HelpersFunctions.noOfInvalidAndRequiredInputs(this.form);
      this.changeDescriptionInfo(count == 0 ? true : false, count);
    } else this.changeDescriptionInfo(true, 0);
  }
}
