import { Component } from "@angular/core";
import { MatDialogRef } from "@angular/material/dialog";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { SvgManager } from "app/shared/resources/assets-manager";

@Component({
  selector: "app-add-candidate-done-dialog",
  templateUrl: "./add-candidate-done-dialog.component.html",
  styleUrls: ["./add-candidate-done-dialog.component.scss"],
})
export class AddCandidateDoneDialogComponent extends AppHelpers {
  svgManager: SvgManager;

  constructor(public dialogRef: MatDialogRef<AddCandidateDoneDialogComponent>) {
    super();

    dialogRef.backdropClick().subscribe(() => this.backToHome());
  }

  backToHome() {
    location.reload();
  }
}
