import {
  ICandidateReferenceDisplayModel,
  ICandidateReferenceModel,
} from "app/recruitment/models/candidate/work-experience/reference";

export function candidateReferenceToDisplay(
  data: ICandidateReferenceModel[]
): ICandidateReferenceDisplayModel[] {
  return data.map((reference) => {
    return <ICandidateReferenceDisplayModel>{
      ...reference,
      company: reference.company_Name,
      reference: reference.refrence_Name,
      mobileNumber: reference.phone,
      notes: reference.note,
    };
  });
}
