import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { IJobTitleModel } from "app/org-management/model/job-title";
import { ITabDescriptionInfo } from "app/recruitment/components/candidate/add-candidate/shared-candidate-components/candidate-tab-description/candidate-tab-description.component";
import { candidateSuitableRequisitionsToDisplay } from "app/recruitment/mapper/candidate/candidate-suitable-requisitions";
import {
  ICandidateSuitableRequisitionsDisplayModel,
  ICandidateSuitableRequisitionsModel,
  ICandidateSuitableRequisitionsRequest,
} from "app/recruitment/models/candidate/work-experience/suitable-requisitions";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { IOrganizationTypeModel } from "app/system-setup/org-management-setup/models/organization-types";
import { IOrganizationModel } from "app/system-setup/org-management-setup/models/organizations";

@Component({
  selector: "candidate-suitable-requisitions-form",
  templateUrl: "./candidate-suitable-requisitions-form.component.html",
  styleUrls: ["./candidate-suitable-requisitions-form.component.scss"],
})
export class CandidateSuitableRequisitionsFormComponent
  extends AppHelpers
  implements OnInit
{
  @Input() candidateId: number = 0;
  @Output() changeDescription = new EventEmitter<ITabDescriptionInfo>();

  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    // "Ser",
    "organization",
    "type",
    "job",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<ICandidateSuitableRequisitionsDisplayModel>;

  selectedOrgType: IOrganizationTypeModel;
  selectedOrg: IOrganizationModel;
  selectedJob: IJobTitleModel;

  constructor(private candidateService: CandidateService) {
    super();

    this.dataSource = new MatTableDataSource([]);
    this.candidateService.submitted$.subscribe((submitted) => {
      if (this.dataSource.data.length == 0) this.submitted = submitted;
    });
  }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      orgTypeId: [null, [Validators.required]],
      org_ID: [null, [Validators.required]],
      job_Title_ID: [null, [Validators.required]],
    });

    this.dataSource = new MatTableDataSource([]);
    this.invalidRequiredCount();
    this.isLoaded = true;
  }

  get formCtrls() {
    return this.form.controls;
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ICandidateSuitableRequisitionsRequest = {
        // person_Instance_ID: this.candidateId,
        id: this.form.value.id == 0 ? this.getNewId() : this.form.value.id,
        org_ID: this.form.value.org_ID,
        job_Title_ID: this.form.value.job_Title_ID,
      };

      const indexModel: ICandidateSuitableRequisitionsModel = {
        ...model,
        orgTypeId: this.form.value.orgTypeId,
        core_Org_TYPE: this.selectedOrgType,
        core_Org: this.selectedOrg,
        core_Job_Title: this.selectedJob,
      };

      this.form.value.id == 0
        ? this.create(indexModel)
        : this.update(indexModel);
    }
    // else this.toastr.error("General.formNotValid");
  }

  create(model: ICandidateSuitableRequisitionsModel) {
    this.dataSource.data.push(
      candidateSuitableRequisitionsToDisplay([model])[0]
    );
    this.clearForm();
    this.invalidRequiredCount();
    this.updateGeneralData();
  }

  update(model: ICandidateSuitableRequisitionsModel) {
    let mapedModel: ICandidateSuitableRequisitionsDisplayModel =
      candidateSuitableRequisitionsToDisplay([model])[0];
    this.dataSource.data[this.getIndex(mapedModel)] = mapedModel;
    this.clearForm();
    this.updateGeneralData();
  }

  candidateSuitableRequisitionsToEdit(
    JobInfo: ICandidateSuitableRequisitionsDisplayModel
  ) {
    this.form.patchValue(JobInfo);
    this.formCtrls.org_ID.setValue(JobInfo.org_ID);
    setTimeout(() => {
      this.formCtrls.job_Title_ID.setValue(JobInfo.job_Title_ID);
    }, 50);
  }

  onDelete(JobInfo: ICandidateSuitableRequisitionsDisplayModel): void {
    let msg = `${JobInfo.organization} - ${JobInfo.job}`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(JobInfo);
    });
  }

  delete(JobInfo: ICandidateSuitableRequisitionsDisplayModel) {
    this.dataSource.data.splice(this.getIndex(JobInfo), 1);
    this.invalidRequiredCount();
    if (JobInfo.id == this.formCtrls.id.value) this.clearForm();
    this.updateGeneralData();
  }

  updateGeneralData() {
    let generalModel: ICandidateSuitableRequisitionsRequest[] = [];
    this.dataSource.data.forEach((ele) => {
      let JobInfo = { ...ele };
      delete JobInfo.id;
      delete JobInfo.orgTypeId;
      delete JobInfo.type;
      delete JobInfo.organization;
      delete JobInfo.job;
      delete JobInfo.core_Org_TYPE;
      delete JobInfo.core_Org;
      delete JobInfo.core_Job_Title;
      generalModel.push(JobInfo);
    });
    this.candidateService.candidateData$.next({
      ...this.candidateService.candidateData$.getValue(),
      JobInfoData: generalModel,
    });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

  getIndex(JobInfo: ICandidateSuitableRequisitionsDisplayModel): number {
    return this.dataSource.data.findIndex((ele) => ele.id == JobInfo.id);
  }

  getNewId(): number {
    return (
      this.dataSource.data.reduce(
        (acc, ele) => (acc = acc > ele.id ? acc : ele.id),
        0
      ) + 1
    );
  }

  onChangeOrganizationType(values: IOrganizationTypeModel[]) {
    this.selectedOrgType = values.length > 0 ? values[0] : null;
    this.invalidRequiredCount();
  }

  onChangeOrganization(values: IOrganizationModel[]) {
    this.selectedOrg = values?.length > 0 ? values[0] : this.selectedOrg;
    this.invalidRequiredCount();
  }

  onChangeJob(values: IJobTitleModel[]) {
    this.selectedJob = values?.length > 0 ? values[0] : this.selectedJob;
    this.invalidRequiredCount();
  }

  descriptionInfo: ITabDescriptionInfo;
  changeDescriptionInfo(isDone: boolean, noOfRequiredInputs: number) {
    this.changeDescription.emit({      isDone: isDone,
      noOfRequiredInputs: noOfRequiredInputs,
    });

    this.candidateService.isDone$.next({
      ...this.candidateService.isDone$.getValue(),
      // JobInfoData: this.descriptionInfo.isDone,
      JobInfoData:isDone,

    });
  }

  invalidRequiredCount() {
    if (!(this.dataSource?.data?.length > 0)) {
      let count = HelpersFunctions.noOfInvalidAndRequiredInputs(this.form);
      this.changeDescriptionInfo(count == 0 ? true : false, count);
    } else this.changeDescriptionInfo(true, 0);
  }
}
