import {
  ICandidateTrainingDisplayModel,
  ICandidateTrainingModel,
} from "app/recruitment/models/candidate/education/training";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";

export function candidateTrainingToDisplay(
  data: ICandidateTrainingModel[]
): ICandidateTrainingDisplayModel[] {
  return data.map((training) => {
    return <ICandidateTrainingDisplayModel>{
      ...training,
      institution: training.center_Name,
      course: training.course_Name,
      startDate: HelpersFunctions.mapDate(training.from_Date),
      endDate: HelpersFunctions.mapDate(training.to_Date),
      notes: training.note,
    };
  });
}
