import { Component, EventEmitter, OnInit, Output } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CandidateContactDialogComponent } from "../candidate-contact-dialog/candidate-contact-dialog.component";
import { IGUIModel } from "app/shared/interfaces/user-views";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "candidate-contact-card",
  templateUrl: "./candidate-contact-card.component.html",
  styleUrls: ["./candidate-contact-card.component.scss"],
})
export class CandidateContactCardComponent
  extends AppHelpers
  implements OnInit
{
  isLoaded: boolean = true;
  @Output() onClickIcon? = new EventEmitter<any>();
  isTableView: boolean = false;
  candidacontactId: number = 2;
  allowedSections: IGUIModel[];

  constructor() {
    super();
  }

  ngOnInit(): void {}
  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.candidate_contact_info_sec
    );
  }
  openEditDialog() {
    let dialogRef = this.dialogsService.addEditDialog(
      CandidateContactDialogComponent
    );
  }
  click($event) {
    this.onClickIcon.emit($event);
  }
  toggleTable() {
    this.isTableView = !this.isTableView;
  }
}
