import { Component, OnInit } from '@angular/core';
import { IGUIModel } from 'app/shared/interfaces/user-views';
import { AppHelpers } from 'app/shared/resources/app-helpers';
import { ViewsManager } from 'app/shared/resources/views-manager';
@Component({
  selector: 'candidate-document-core',
  templateUrl: './candidate-document-core.component.html',
  styleUrls: ['./candidate-document-core.component.scss']
})
export class CandidateDocumentCoreComponent  extends AppHelpers implements OnInit {
	allowedSections: IGUIModel[];
	viewsManager = ViewsManager;
	constructor() {
		super();
	}

  ngOnInit(): void {
  }
  override onModulesChanged() {
		this.allowedSections = this.getCustomViewChildren(
			this.getParentViewName,
			ViewsManager.candidate_Documents_info_tab
		);
	}

}
