import {
  ICandidateDocumentsDisplayModel,
  ICandidateDocumentsModel,
} from "app/recruitment/models/candidate/work-experience/documents";
import { msgTranslate } from "app/shared/resources/helpers-functions";

export function candidateDocumentsToDisplay(
  data: ICandidateDocumentsModel[]
): ICandidateDocumentsDisplayModel[] {
  return data.map((document) => {
    return <ICandidateDocumentsDisplayModel>{
      ...document,
      attachmentType: msgTranslate(
        document.comm_Attachment_Type?.ar_Name,
        document.comm_Attachment_Type?.en_Name
      ),
    };
  });
}
