import { Component, OnInit } from '@angular/core';
import { IGUIModel } from 'app/shared/interfaces/user-views';
import { AppHelpers } from 'app/shared/resources/app-helpers';
import { ViewsManager } from 'app/shared/resources/views-manager';

@Component({
  selector: 'candidate-contact-core',
  templateUrl: './candidate-contact-core.component.html',
  styleUrls: ['./candidate-contact-core.component.scss']
})
export class CandidateContactCoreComponent extends AppHelpers implements OnInit {
	allowedSections: IGUIModel[];
	viewsManager = ViewsManager;
	constructor() {
		super();
	}

  ngOnInit(): void {
  }
  override onModulesChanged() {
		this.allowedSections = this.getCustomViewChildren(
			this.getParentViewName,
			ViewsManager.candidate_contact_info_sec
		);
	}
}
