import { Component, OnInit } from "@angular/core";
import { MatDialogRef } from "@angular/material/dialog";

@Component({
  selector: "app-candidate-general-info-dialog",
  templateUrl: "./candidate-general-info-dialog.component.html",
  styleUrls: ["./candidate-general-info-dialog.component.scss"],
})
export class CandidateGeneralInfoDialogComponent implements OnInit {
  constructor(
    public dialogRef: MatDialogRef<CandidateGeneralInfoDialogComponent>
  ) {}

  ngOnInit(): void {}
  
  onCloseDialog() {
    this.dialogRef.close();
  }
}
