import { Component, OnInit } from '@angular/core';
import { AppHelpers } from 'app/shared/resources/app-helpers';
import { ViewsManager } from "app/shared/resources/views-manager";
import { IGUIModel } from "app/shared/interfaces/user-views";

@Component({
  selector: 'candidate-general-info-core',
  templateUrl: './candidate-general-info-core.component.html',
  styleUrls: ['./candidate-general-info-core.component.scss']
})
export class CandidateGeneralInfoCoreComponent  extends AppHelpers implements OnInit {
	allowedSections: IGUIModel[];
	viewsManager = ViewsManager;
	constructor() {
		super();
	}

  ngOnInit(): void {
  }
  override onModulesChanged() {
		this.allowedSections = this.getCustomViewChildren(
			this.getParentViewName,
			ViewsManager.candidate_general_info_tab
		);
	}
}
