import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { IGUIModel } from 'app/shared/interfaces/user-views';
import { AppHelpers } from 'app/shared/resources/app-helpers';
import { ViewsManager } from 'app/shared/resources/views-manager';
import { CandidateSuitableRequisitionsDialogComponent } from '../candidate-suitable-requisitions-dialog/candidate-suitable-requisitions-dialog.component';
@Component({
  selector: 'candidate-suitable-requisitions-card',
  templateUrl: './candidate-suitable-requisitions-card.component.html',
  styleUrls: ['./candidate-suitable-requisitions-card.component.scss']
})
export class CandidateSuitableRequisitionsCardComponent extends AppHelpers  implements OnInit {
  isLoaded: boolean = true;
  allowedSections: IGUIModel[];
  @Output() onClickIcon? = new EventEmitter<any>();
  isTableView: boolean = false;
  candidateSutitableId: number = 2;


  constructor() {
    super();
  }
  ngOnInit(): void {
  }
  override onModulesChanged() {
    this.allowedSections = this.getCustomViewChildren(
      this.getParentViewName,
      ViewsManager.candidate_Suitable_Requisitions_info_sec
    );

  }

  openEditDialog(){

    let dialogRef = this.dialogsService.addEditDialog(
      CandidateSuitableRequisitionsDialogComponent
    );
  }
  click($event) {
    this.onClickIcon.emit($event);
  }
  toggleTable() {
    this.isTableView = !this.isTableView;
  }

}
