import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-candidate-education-dialog',
  templateUrl: './candidate-education-dialog.component.html',
  styleUrls: ['./candidate-education-dialog.component.scss']
})
export class CandidateEducationDialogComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<CandidateEducationDialogComponent>
  ) {}

  ngOnInit(): void {}
  onCloseDialog() {
    this.dialogRef.close();
  }

}
