import {
  ICandidateSuitableRequisitionsDisplayModel,
  ICandidateSuitableRequisitionsModel,
} from "app/recruitment/models/candidate/work-experience/suitable-requisitions";
import { msgTranslate } from "app/shared/resources/helpers-functions";

export function candidateSuitableRequisitionsToDisplay(
  data: ICandidateSuitableRequisitionsModel[]
): ICandidateSuitableRequisitionsDisplayModel[] {
  return data.map((jobInfo) => {
    return <ICandidateSuitableRequisitionsDisplayModel>{
      ...jobInfo,
      organization: msgTranslate(
        jobInfo?.core_Org?.ar_Name,
        jobInfo?.core_Org?.en_Name
      ),
      type: msgTranslate(
        jobInfo?.core_Org_TYPE?.ar_Name,
        jobInfo?.core_Org_TYPE?.en_Name
      ),
      job: msgTranslate(
        jobInfo?.core_Job_Title?.ar_Name,
        jobInfo?.core_Job_Title?.en_Name
      ),
    };
  });
}
